.cartContainer {
    display: flex;
    justify-content: space-around;
    background-color: white;
    margin: 40px;
    padding: 5px;
    border-radius: 10px;
    box-shadow: 0 0 5px 10px white;
}

.cartName {
    font-family: 'Modak', cursive;
    color: black;
    margin: 5px, 0;
}

.cartQty {
    font-family: 'Modak', cursive;
    color: black;
    margin: 5px, 0;
    padding: 5px;
}

.cartQty input {
    font-weight: 800;
    color: black;
    padding: 2px;
}

.conversation {
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    margin: 20px 0;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 0 5px 10px white;
    width: 90%;
}

.conversation:hover {
    background-color: lightgray;
}

.conversationImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
}

.conversationName {
    font-weight: 500;
}
.message {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}

.messageTop {
    display: flex;
}

.messageText {
    padding: 5px;
    margin: 5px;
    border-radius: 20px;
    background-color: #20639b;
    box-shadow: 0 0 5px 5px #20639b;
    color: white;
    max-width: 300px;
}

.messageBottom {
    font-size: 12px;
    margin-top: 10px;
}

.message.own {
    align-items: flex-end;
}

.message.own .messageText {
    background-color: lightgray;
    color: black;
    box-shadow: 0 0 5px 5px lightgray;
}
